import React, {Component} from "react";
import FilterOptions from "./FilterOptions/FilterOptions";

interface IFilterOptionsWrapperState {
	isModal: boolean;
	options: {
		sale: boolean;
		shipping: {
			free: boolean;
			fast: boolean;
			slow: boolean;
		};
		location: {
			anyLocation: boolean;
			vietnam: boolean;
			customLocation: boolean;
		};
		itemType: {
			all: boolean;
			handmade: boolean;
			vintage: boolean;
		};
		price: {
			anyPrice: boolean;
			u25: boolean;
			t2550: boolean;
			t50100: boolean;
			o100: boolean;
			customPrice: boolean;
		};
		ordering: {
			giftCard: boolean;
			wrapped: boolean;
		};
	};
}
class FilterOptionsWrapper extends Component {
	state: IFilterOptionsWrapperState;

	constructor(props: any) {
		super(props);
		this.state = {
			isModal: false,
			options: {
				sale: false,
				shipping: {
					free: false,
					fast: false,
					slow: false
				},
				location: {
					anyLocation: true,
					vietnam: false,
					customLocation: false
				},
				itemType: {
					all: true,
					handmade: false,
					vintage: false
				},
				price: {
					anyPrice: true,
					u25: false,
					t2550: false,
					t50100: false,
					o100: false,
					customPrice: false
				},
				ordering: {
					giftCard: false,
					wrapped: false
				}
			}
		};
	}

	componentDidMount = () => {
		$("#optionReveal").on("closed.zf.reveal", (e) => {
			this.modalToggler();
		});
	};

	modalToggler = () => {
		this.setState((prevState: IFilterOptionsWrapperState) => {
			return {
				isModal: !prevState.isModal
			};
		});
	};

	filterToggler = (name: string, type: string) => {
		this.setState((prevState: IFilterOptionsWrapperState) => {
			const updatedState = {};
			const options = prevState.options;
			Object.keys(options).forEach((filter) => {
				if (filter === name) {
					updatedState[filter] = !options[filter];
				} else if (Object.keys(options[filter]).indexOf(name) > -1) {
					const subState = options[filter];
					updatedState[filter] = {};
					Object.keys(subState).forEach((subFilter) => {
						if (subFilter === name) {
							updatedState[filter][subFilter] = !subState[subFilter];
						} else if (type === "checkbox") {
							updatedState[filter][subFilter] = subState[subFilter];
						} else {
							updatedState[filter][subFilter] = false;
						}
					});
				} else {
					updatedState[filter] = options[filter];
				}
			});
			return {
				options: updatedState
			};
		});
	};

	render() {
		return (
			<div>
				<div className="hide-for-small-only">
					{!this.state.isModal ? (
						<FilterOptions
							options={this.state.options}
							filterToggler={this.filterToggler}
						/>
					) : (
						""
					)}
				</div>
				<div className="reveal small" id="optionReveal" data-reveal>
					<br />
					{this.state.isModal ? (
						<FilterOptions
							options={this.state.options}
							filterToggler={this.filterToggler}
						/>
					) : (
						""
					)}
					<button
						type="button"
						className="close-button"
						data-close=""
						aria-label="close this modal">
						<i className="fas fa-check" />
					</button>
				</div>
				<button
					type="button"
					className="button expanded show-for-small-only"
					onClick={this.modalToggler}
					data-toggle="optionReveal">
					Options
				</button>
			</div>
		);
	}
}

export default FilterOptionsWrapper;
