import React, {Component} from "react";
import uuid from "uuid";
import menu from "../../menu.json";
import {Link} from "react-router-dom";

class OffCanvasMenu extends Component {
	render() {
		const submenuGenerator = (menuObj) => {
			return Object.keys(menuObj).map((menuKey) => {
				const children = menuObj[menuKey].children;
				return (
					<li className="cell" key={uuid.v4()}>
						<Link to={"/category/" + menuObj[menuKey].link}>{menuKey}</Link>
						{children && Object.keys(children).length > 0 ? (
							<ul className="menu vertical grid-x">
								{Object.keys(children).length > 1 ? (
									<li>
										<hr className="margin-0" />
										<Link to={"/category/" + menuObj[menuKey].link}>
											ALL {menuKey.toLocaleUpperCase()}
										</Link>
										<hr className="margin-0" />
									</li>
								) : (
									""
								)}
								{submenuGenerator(children)}
							</ul>
						) : (
							""
						)}
					</li>
				);
			});
		};

		return (
			<div className="margin-1">
				<br />
				<p className="lead">BROWSE CATEGORIES</p>
				<ul
					className="menu vertical drilldown grid-x"
					data-drilldown
					data-animate-height="true"
					data-back-button-position="bottom"
					data-animation-duration="0">
					{submenuGenerator(menu)}
				</ul>
				<button
					type="button"
					className="close-button"
					data-close=""
					aria-label="close this off-canvas menu">
					<span className="h3" aria-hidden="true">
						&times;
					</span>
				</button>
			</div>
		);
	}
}

export default OffCanvasMenu;
